<script setup lang="ts">
import { ref, reactive, onMounted } from 'vue';

// Props
interface Props {
  formId?: string;
  fallbackForm?: boolean;
}

const props = withDefaults(defineProps<Props>(), {
  formId: '65f8b8b8c8b8b8b8b8b8b8b8', // Default Yandex Form ID
  fallbackForm: true
});

// Reactive data
const isLoading = ref(true);
const hasError = ref(false);
const showFallbackForm = ref(false);
const isSubmitting = ref(false);
const isSubmitted = ref(false);

const formData = reactive({
  name: '',
  email: '',
  phone: '',
  program: '',
  message: '',
  consent: false
});

// Computed form URL
const formUrl = ref('');

// Methods
const onFormLoad = () => {
  isLoading.value = false;
  hasError.value = false;
};

const submitFallbackForm = async () => {
  isSubmitting.value = true;
  
  try {
    // Simulate form submission
    await new Promise(resolve => setTimeout(resolve, 2000));
    
    // In a real implementation, you would send the data to your backend
    console.log('Form data:', formData);
    
    // Show success message
    isSubmitted.value = true;
    
    // Reset form after 3 seconds
    setTimeout(() => {
      isSubmitted.value = false;
      Object.assign(formData, {
        name: '',
        email: '',
        phone: '',
        program: '',
        message: '',
        consent: false
      });
    }, 3000);
    
  } catch (error) {
    console.error('Form submission error:', error);
    alert('–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞ –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–∫–µ —Ñ–æ—Ä–º—ã. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â–µ —Ä–∞–∑.');
  } finally {
    isSubmitting.value = false;
  }
};

const initializeForm = () => {
  if (props.formId) {
    // Construct Yandex Form URL
    formUrl.value = `https://forms.yandex.ru/cloud/${props.formId}/?embedded=1`;
    
    // Set timeout to show fallback form if iframe doesn't load
    setTimeout(() => {
      if (isLoading.value) {
        hasError.value = true;
        isLoading.value = false;
        if (props.fallbackForm) {
          showFallbackForm.value = true;
        }
      }
    }, 10000); // 10 seconds timeout
  } else if (props.fallbackForm) {
    // Show fallback form directly if no Yandex Form ID provided
    isLoading.value = false;
    showFallbackForm.value = true;
  }
};

// Lifecycle
onMounted(() => {
  initializeForm();
});
</script>

